___CSS_LOADER_EXPORT___.push([module.id, `.restoran-card{
    width: 427px;
    min-width: 427px;   /* Минимальная ширина */
    max-width: 427px;
    height: 431px;
    border-radius: 8px;
    gap: 16px;
    background-color: #F9F9F9;

  }
  .restoran_img{
    width: 427px;
    height: 240px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
  }
  .restoran-card-content{
    width: 356px;
    height: 175px;
    padding-right: 16px;
    padding-bottom: 16px;
    padding-left: 16px;
    gap: 8px;
    margin-top: 16px;
  }
  .restoran-card-title{
    text-decoration: none; /* Убираем подчеркивание */
    color: inherit; /* Сохраняем цвет текста родительского элемента */
    border: none; /* Убираем рамку */
    cursor: pointer; /* Добавляем указатель мыши в виде руки для кликабельности */
    text-align: center; /* Выравнивание текста по горизонтали */
    height: 32px;
    font-weight: 700;
    font-size: 22.5px;
    line-height: 32px;
    letter-spacing: 0;
    margin-bottom: 8px;
    margin-top: 0;
    display: flex;
    align-items: center;
  }
  .restoran-card-review{
    display: flex;
    height: 26px;
    font-family: var(--font-Hind);
    font-weight: 400;
    font-size: 16px;
    line-height: 100%;
    letter-spacing: 0;
    color:#596F72;
    margin-bottom: 8px;
    margin-top: 0;
  }
  .review-text{
    padding-top: 7px;
    margin: 0;
  }
  .star{
    width: 22px;
    height: 22px;
    padding-top: 3px;
    padding-left: 2px;
  }
  .restoran-card-description{
    height: 29px;
    font-family: var(--font-Hind);
    font-weight: 400;
    font-size: 18px;
    line-height: 100%;
    letter-spacing: 0;
    color: #4D4D4D;
    margin: 0;
    display: flex;
    align-items: center;
  }
  .restoran-card-category{
    height: 48px;
    padding-top: 16px;
    gap: 10px;
    margin: 0;
    display: flex;
    align-items: center;
  }
  .btn-category{
    text-decoration: none; /* Убираем подчеркивание */
    border: none; /* Убираем рамку */
    cursor: pointer; /* Добавляем указатель мыши в виде руки для кликабельности */
    text-align: center; /* Выравнивание текста по горизонтали */
    width: 110px;
    height: 32px;
    font-family: var(--font-Hind);
    font-weight: 400;
    font-size: 16px;
    line-height: 32px;
    letter-spacing: 0;
    background-color: #E9E9E9;
    color: #636363;
    margin: 0;
  }
  @media (max-width: 464px) {
    .restoran-card{
      width: 328px;
      min-width: 328px;   /* Минимальная ширина */
      max-width: 328px;
    }
    .restoran_img{
      width: 328px;  
    }
    .restoran-card-content{
      width: 328px;
    }
