___CSS_LOADER_EXPORT___.push([module.id, `.all-restorans-page-container {
    max-width: 1200px;
    margin: 40px auto;
    padding: 0 20px;
}

.all-restorans-title {
    font-size: 36px;
    font-weight: 700;
    color: #202020;
    text-align: center;
    margin-bottom: 40px;
}

.all-restorans-grid {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px; /* Отступ между карточками */
}

/* Адаптивность для меньших экранов */
@media (max-width: 768px) {
    .all-restorans-grid {
        gap: 20px;
    }
    .all-restorans-title {
        font-size: 28px;
        margin-bottom: 30px;
    }
}

@media (max-width: 480px) {
    .all-restorans-grid {
        grid-template-columns: 1fr;
        gap: 15px;
    }
    .all-restorans-title {
        font-size: 24px;
        margin-bottom: 20px;
    }
    .all-restorans-page-container {
        margin: 20px auto;
        padding: 0 15px;
    }
} `, "",{"version":3,"sources":["webpack://./src/pages/allrestorans/style.css"],"names":[],"mappings":"AAAA;IACI,iBAAiB;IACjB,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,cAAc;IACd,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS,EAAE,4BAA4B;AAC3C;;AAEA,qCAAqC;AACrC;IACI;QACI,SAAS;IACb;IACA;QACI,eAAe;QACf,mBAAmB;IACvB;AACJ;;AAEA;IACI;QACI,0BAA0B;QAC1B,SAAS;IACb;IACA;QACI,eAAe;QACf,mBAAmB;IACvB;IACA;QACI,iBAAiB;QACjB,eAAe;IACnB;AACJ","sourcesContent":[".all-restorans-page-container {\n    max-width: 1200px;\n    margin: 40px auto;\n    padding: 0 20px;\n}\n\n.all-restorans-title {\n    font-size: 36px;\n    font-weight: 700;\n    color: #202020;\n    text-align: center;\n    margin-bottom: 40px;\n}\n\n.all-restorans-grid {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 30px; /* Отступ между карточками */\n}\n\n/* Адаптивность для меньших экранов */\n@media (max-width: 768px) {\n    .all-restorans-grid {\n        gap: 20px;\n    }\n    .all-restorans-title {\n        font-size: 28px;\n        margin-bottom: 30px;\n    }\n}\n\n@media (max-width: 480px) {\n    .all-restorans-grid {\n        grid-template-columns: 1fr;\n        gap: 15px;\n    }\n    .all-restorans-title {\n        font-size: 24px;\n        margin-bottom: 20px;\n    }\n    .all-restorans-page-container {\n        margin: 20px auto;\n        padding: 0 15px;\n    }\n} "],"sourceRoot":""}]);
