  background-color: #E5F8BC;
  height: 193px;
  text-align: center;
  padding: 56px;
  width: 100%;
}

.order-banner-title {
  width: 539px;
  height: 48px;
  font-weight: 700;
  font-size: 35.16px;
  line-height: 48px;
  letter-spacing: -0.02em;
  color: #202020;
}

.order-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 20px;
}
.order-main-row{
  display: flex;
  gap: 24px;
  margin-top: -169px;
}
.order-form-wrapper{
  width: 877px;
  height: 528px;
  background: #fff;
  border-radius: 4px;
  margin-left: 56px;
  min-width: 877px;
  max-width: 877px;
  box-sizing: border-box;
}
.order-form{
  margin: 24px;
}
.order-form-header-row {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  height: 32px;
}
.order-form-title {
  font-weight: 700;
  font-size: 22.5px;
  line-height: 32px;
  letter-spacing: 0;
  color: #202020;
}
.order-form-step {
  font-family: var(--font-Hind);
  font-weight: 400;
  font-size: 18px;
  line-height: 100%;
  letter-spacing: 0;
  color: #202020;
}
.order-form-underline {
  margin-top: 16px;
  width: 100%;
  display: flex;
  gap: 0;
}
.order-form-underline-1 {
  width: 40%;
  border-bottom: 6px solid #202020;
}
.order-form-underline-2 {
  width: 100%;
  border-bottom: 4px solid #F5F6F7;
}
.order-form-row {
  display: flex;
  gap: 24px;
  margin-top: 32px;
  height: 81px;
  margin-bottom: 16px;
}
.order-form-group {
  display: flex;
  flex-direction: column;
  width: 50%;
}
.order-form-group label {
  font-family: var(--font-Hind);
  font-weight: 400;
  font-size: 18px;
  line-height: 29px;
  letter-spacing: 0;
  color: #363636;
  margin-bottom: 0;
}
.order-form-group input {
  background: #F5F6F7;
  height: 48px;
  border: none;
  font-family: var(--font-Hind);
  font-weight: 400;
  font-size: 18px;
  line-height: 48px;
  letter-spacing: 0;
  padding-left: 16px;
}
.order-form-group-width {
  width: 100%;
  margin-bottom: 16px;
}
.order-form-hint {
  font-family: var(--font-Hind);
  font-weight: 400;
  font-size: 11.52px;
  line-height: 100%;
  letter-spacing: 0.02em;
  color: #4D4D4D;
  margin-bottom: 32px;
  height: 18px;
}
.order-next-btn {
  background-color: #202020;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.3s ease;
  height: 55px;
  padding: 13px 24px;
  font-family: var(--font-Hind);
  font-weight: 400;
  font-size: 18px;
  line-height: 29px;
  letter-spacing: 0;
}
.submit-button:hover {
  background-color: #404040;
}
.order-form-group input:focus {
  outline: 2px solid #2196f3;
  background: #fff;
}

/* Стили для ошибок валидации */
.order-form-group input.error {
  border: 2px solid #ff4444;
  background: #fff5f5;
}

.error-message {
  color: #ff4444;
  font-size: 14px;
  margin-top: 4px;
  font-family: var(--font-Hind);
  font-weight: 400;
  line-height: 1.2;
}

.order-form-group input.error:focus {
  outline: 2px solid #ff4444;
  background: #fff5f5;
}

.order-next-btn:hover {
  background: #444;
}

label {
  display: block;
  margin-bottom: 8px;
  color: #4a4a4a;
  font-weight: 500;
}

input[type="text"],
input[type="email"],
input[type="tel"],
select,
textarea {
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
select:focus,
textarea:focus {
  border-color: #202020;
  outline: none;
}

@media (max-width: 1250px) {
  .order-banner {
    text-align: center;
    padding-left: 0;
    padding-right: 0;
  }
  .order-banner-title {
    width: 100%;
    text-align: center;
  }
  .order-main-row {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  .order-form-wrapper {
    order: 2;
    margin-left: 0;
    width: 100%;
  }
  .order-main-row > div:last-child {
    order: 1;
    margin-left: 0;
  }
}
@media (max-width: 880px) {
  .order-form-wrapper {
    min-width: unset;
    max-width: unset;
  }
  .order-form{
    margin-left: 16px;
    margin-right: 16px;
  }
  .order-form-row {
    flex-direction: column;
    gap: 16px;
    height: auto;
    padding-bottom: 10px;
  }
  .order-form-group {
    width: 100%;
  }
