___CSS_LOADER_EXPORT___.push([module.id, `.container {
    padding: 0 56px;
  }
  .header {
    display: flex;
    align-items: center;
    color: #202020;
    border: 1px;
    border-color: #E9E9E9;
    height: 72px;
  }
  .logo{
    width: 134.26336669921875px;
    height: 19.935205459594727px;
    margin: 11.39px 9.82px;
  }
  .logo-in-med{
    display: none;
  }
  .btn-menu{
    display: none;
  }
  .header-a{
    display: inline-block; /* Устанавливаем элемент как блочный */
    text-decoration: none; /* Убираем подчеркивание */
    color: inherit; /* Сохраняем цвет текста родительского элемента */
    border: none; /* Убираем рамку */
    cursor: pointer; /* Добавляем указатель мыши в виде руки для кликабельности */
    text-align: center; /* Выравнивание текста по горизонтали */
    border-radius: 4px;
    background-color: #ffffff;
    font-family: var(--font-Hind);
    line-height: 55px;
    font-weight: 400;
    font-size: 18px;
    letter-spacing: 0;
  }
  .btn-home{
    width: 94px;
    height: 55px;
    margin-left: auto;
    margin-right: 8px;
  }
  .btn-all-restaurants{
    width: 159px;
    height: 55px;
    margin-right: 24px;
  }
  .btn-shop{
    width: 56px;
    height: 56px;
    background-color: #202020;
  }
  .shop{
    margin: 16px;
    width: 24px;
    height: 24px;
  }
  .btn-menu{
    background-color: #ffffff;
    border: #ffffff;
  }

  @media (max-width: 1279px) {
    .container {
      padding: 0 16px;
    }
    .header {
      height: 56px;
      padding: 0;
      justify-content: center;
    }
    .logo {
      display: none;
    }
    .btn-home {
      display: none;
    }
    .btn-all-restaurants {
      display: none;
    }
    .btn-shop {
      display: none;
    }
    .logo-in-med {
      display: block;
      width: 42.1685905456543px;
      height: 37.02049255371094px;
      margin-left: 55%;
      transform: translateX(-50%);
    }
    .btn-menu {
      display: block;
      position: absolute;
      left: 16px;
    }
  }

  .dropdown-menu {
    position: absolute;
    top: 60px;
    left: 20px;
    background: #fff;
    border: 1px solid #eee;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.15);
    z-index: 1000;
    min-width: 160px;
  }
  .dropdown-menu ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  .dropdown-menu li {
    border-bottom: 1px solid #f0f0f0;
  }
  .dropdown-menu li:last-child {
    border-bottom: none;
  }
  .dropdown-menu a {
    display: block;
    padding: 12px 20px;
    color: #222;
    text-decoration: none;
    transition: background 0.2s;
  }
  .dropdown-menu a:hover {
    background: #f5f5f5;
  }`, "",{"version":3,"sources":["webpack://./src/widgets/header/style.css"],"names":[],"mappings":"AAAA;IACI,eAAe;EACjB;EACA;IACE,aAAa;IACb,mBAAmB;IACnB,cAAc;IACd,WAAW;IACX,qBAAqB;IACrB,YAAY;EACd;EACA;IACE,2BAA2B;IAC3B,4BAA4B;IAC5B,sBAAsB;EACxB;EACA;IACE,aAAa;EACf;EACA;IACE,aAAa;EACf;EACA;IACE,qBAAqB,EAAE,sCAAsC;IAC7D,qBAAqB,EAAE,0BAA0B;IACjD,cAAc,EAAE,iDAAiD;IACjE,YAAY,EAAE,kBAAkB;IAChC,eAAe,EAAE,4DAA4D;IAC7E,kBAAkB,EAAE,uCAAuC;IAC3D,kBAAkB;IAClB,yBAAyB;IACzB,6BAA6B;IAC7B,iBAAiB;IACjB,gBAAgB;IAChB,eAAe;IACf,iBAAiB;EACnB;EACA;IACE,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,iBAAiB;EACnB;EACA;IACE,YAAY;IACZ,YAAY;IACZ,kBAAkB;EACpB;EACA;IACE,WAAW;IACX,YAAY;IACZ,yBAAyB;EAC3B;EACA;IACE,YAAY;IACZ,WAAW;IACX,YAAY;EACd;EACA;IACE,yBAAyB;IACzB,eAAe;EACjB;;EAEA;IACE;MACE,eAAe;IACjB;IACA;MACE,YAAY;MACZ,UAAU;MACV,uBAAuB;IACzB;IACA;MACE,aAAa;IACf;IACA;MACE,aAAa;IACf;IACA;MACE,aAAa;IACf;IACA;MACE,aAAa;IACf;IACA;MACE,cAAc;MACd,yBAAyB;MACzB,2BAA2B;MAC3B,gBAAgB;MAChB,2BAA2B;IAC7B;IACA;MACE,cAAc;MACd,kBAAkB;MAClB,UAAU;IACZ;EACF;;EAEA;IACE,kBAAkB;IAClB,SAAS;IACT,UAAU;IACV,gBAAgB;IAChB,sBAAsB;IACtB,kBAAkB;IAClB,sCAAsC;IACtC,aAAa;IACb,gBAAgB;EAClB;EACA;IACE,gBAAgB;IAChB,SAAS;IACT,UAAU;EACZ;EACA;IACE,gCAAgC;EAClC;EACA;IACE,mBAAmB;EACrB;EACA;IACE,cAAc;IACd,kBAAkB;IAClB,WAAW;IACX,qBAAqB;IACrB,2BAA2B;EAC7B;EACA;IACE,mBAAmB;EACrB","sourcesContent":[".container {\n    padding: 0 56px;\n  }\n  .header {\n    display: flex;\n    align-items: center;\n    color: #202020;\n    border: 1px;\n    border-color: #E9E9E9;\n    height: 72px;\n  }\n  .logo{\n    width: 134.26336669921875px;\n    height: 19.935205459594727px;\n    margin: 11.39px 9.82px;\n  }\n  .logo-in-med{\n    display: none;\n  }\n  .btn-menu{\n    display: none;\n  }\n  .header-a{\n    display: inline-block; /* Устанавливаем элемент как блочный */\n    text-decoration: none; /* Убираем подчеркивание */\n    color: inherit; /* Сохраняем цвет текста родительского элемента */\n    border: none; /* Убираем рамку */\n    cursor: pointer; /* Добавляе