  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 32px;
  gap: 24px;
  }
  .restoran-block{
    margin: 32px 56px;
  }
  .restorans-title{
    height: 26px;
    font-family: var(--font-Hind);
    font-weight: 400;
    font-size: 16px;
    line-height: 100%;
    letter-spacing: 0%;
    

    color: #797979;
  }
.link-categories{
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  text-decoration: underline;
  text-decoration-style: solid;
  text-underline-offset: 0%;
  text-decoration-thickness: 0%;
}
@media (max-width: 540px) {
  .restoran-block{
    margin: 32px 16px;
  }
  .restorans-block-conteiner {
    display: flex;
    justify-content: left;
}
