___CSS_LOADER_EXPORT___.push([module.id, `.all-restoran-card{
    width: 800px; /* Увеличим ширину для лучшего отображения двух колонок */
    max-width: 100%;
    height: auto; /* Высота будет динамической */
    border-radius: 8px;
    background-color: #F9F9F9;
    display: flex;
    gap: 20px; /* Отступ между изображением и контентом */
    align-items: center;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    padding: 20px;
    box-sizing: border-box; /* Учитываем padding в ширине */
}

.all-restoran-card--reversed {
    flex-direction: row-reverse;
}

.all-restoran_img{
    width: 300px; /* Ширина изображения */
    height: 200px; /* Высота изображения */
    border-radius: 8px;
    object-fit: cover;
    flex-shrink: 0; /* Не сжимать изображение */
}

.all-restoran-card-content{
    flex-grow: 1; /* Описание займет все оставшееся пространство */
    padding: 0; /* Убираем лишние отступы, так как padding уже есть в родительском элементе */
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.all-restoran-card-title{
    text-decoration: none;
    color: #202020;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.2;
}

.all-restoran-card-review{
    display: flex;
    align-items: center;
    gap: 5px;
    color:#596F72;
    font-size: 16px;
}

.all-restoran-card-review .star{
    width: 18px;
    height: 18px;
}

.all-restoran-card-description{
    color: #4D4D4D;
    font-size: 16px;
    line-height: 1.5;
    margin-top: 5px;
}

.all-restoran-card-category{
    margin-top: 10px;
}

.all-restoran-card-category .btn-category{
    text-decoration: none;
    background-color: #E9E9E9;
    color: #636363;
    padding: 5px 15px;
    border-radius: 4px;
    font-size: 14px;
    display: inline-block;
}

/* Адаптивность для меньших экранов */
@media (max-width: 768px) {
    .all-restoran-card {
        flex-direction: column; /* На маленьких экранах всегда вертикально */
        align-items: flex-start;
        padding: 15px;
    }
    .all-restoran-card--reversed {
        flex-direction: column; /* Переопределяем для маленьких экранов */
    }
    .all-restoran_img {
        width: 100%;
        height: 180px;
        margin-bottom: 10px;
    }
    .all-restoran-card-content {
        width: 100%;
    }
    .all-restoran-card-title {
        font-size: 20px;
    }
    .all-restoran-card-review,
    .all-restoran-card-description,
    .all-restoran-card-category {
        font-size: 14px;
    }
} `, "",{"version":3,"sources":["webpack://./src/pages/allrestorans/components/allrestoran_card.css"],"names":[],"mappings":"AAAA;IACI,YAAY,EAAE,yDAAyD;IACvE,eAAe;IACf,YAAY,EAAE,8BAA8B;IAC5C,kBAAkB;IAClB,yBAAyB;IACzB,aAAa;IACb,SAAS,EAAE,0CAA0C;IACrD,mBAAmB;IACnB,qCAAqC;IACrC,aAAa;IACb,sBAAsB,EAAE,+BAA+B;AAC3D;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,YAAY,EAAE,uBAAuB;IACrC,aAAa,EAAE,uBAAuB;IACtC,kBAAkB;IAClB,iBAAiB;IACjB,cAAc,EAAE,2BAA2B;AAC/C;;AAEA;IACI,YAAY,EAAE,gDAAgD;IAC9D,UAAU,EAAE,6EAA6E;IACzF,aAAa;IACb,sBAAsB;IACtB,QAAQ;AACZ;;AAEA;IACI,qBAAqB;IACrB,cAAc;IACd,gBAAgB;IAChB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;IACR,aAAa;IACb,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,cAAc;IACd,eAAe;IACf,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,qBAAqB;IACrB,yBAAyB;IACzB,cAAc;IACd,iBAAiB;IACjB,kBAAkB;IAClB,eAAe;IACf,qBAAqB;AACzB;;AAEA,qCAAqC;AACrC;IACI;QACI,sBAAsB,EAAE,4CAA4C;QACpE,uBAAuB;QACvB,aAAa;IACjB;IACA;QACI,sBAAsB,EAAE,yCAAyC;IACrE;IACA;QACI,WAAW;QACX,aAAa;QACb,mBAAmB;IACvB;IACA;QACI,WAAW;IACf;IACA;QACI,eAAe;IACnB;IACA;;;QAGI,eAAe;IACnB;AACJ","sourcesContent":[".all-restoran-card{\n    width: 800px; /* Увеличим ширину для лучшего отображения двух колонок */\n    max-width: 100%;\n    height: auto; /* Высота будет динамической */\n    border-radius: 8px;\n    background-color: #F9F9F9;\n    display: flex;\n    gap: 20px; /* Отступ между изображением и контентом */\n    align-items: center;\n    box-shadow: 0 4px 8px rgba(0,0,0,0.1);\n    padding: 20px;\n    box-sizing: border-box; /* Учитываем padding в ширине */\n}\n\n.all-restoran-card--reversed {\n    flex-direction: row-reverse;\n}\n\n.all-restoran_img{\n    width: 300px; /* Ширина изображения */\n    height: 200px; /* Высота изображения */\n    border-radius: 8px;\n    object-fit: cover;\n    flex-shrink: 0; /* Не сжимать изображение */\n}\n\n.all-restoran-card-content{\n    flex-grow: 1; /* Описание займет все оставшееся пространство */\n    padding: 0; /* Убираем лишние 