___CSS_LOADER_EXPORT___.push([module.id, `.order-summary-wrapper {
    min-width: 280px;
    max-width: 340px;
    flex: 1;
    display: flex;
    align-items: flex-start;
  }
  .order-summary {
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.06);
    padding: 28px 28px 18px 28px;
    width: 100%;
  }
  .order-summary-title {
    font-size: 22px;
    font-weight: 700;
    margin-bottom: 18px;
  }
  .order-summary-list {
    border-bottom: 1px solid #eee;
    margin-bottom: 18px;
  }
  .order-summary-item {
    display: flex;
    align-items: center;
    font-size: 16px;
    margin-bottom: 10px;
    gap: 12px;
  }
  .order-summary-qty {
    min-width: 18px;
    font-weight: 500;
  }
  .order-summary-name {
    flex: 1;
  }
  .order-summary-price {
    font-weight: 500;
    min-width: 60px;
    text-align: right;
  }
  .order-summary-total-row {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    font-weight: 700;
    margin-top: 10px;
  }
  .order-summary-total {
    font-size: 22px;
    font-weight: 700;
  }
  .restaurant-group {
    margin-bottom: 20px;
    border-bottom: 1px solid #eee;
    padding-bottom: 15px;
  }
  .restaurant-group:last-child {
    border-bottom: none;
    margin-bottom: 0;
    padding-bottom: 0;
  }
  .restaurant-title {
    font-weight: bold;
    font-size: 1.1em;
    margin-bottom: 10px;
    color: #333;
  }`, "",{"version":3,"sources":["webpack://./src/pages/form/order-shop/style.css"],"names":[],"mappings":"AAAA;IACI,gBAAgB;IAChB,gBAAgB;IAChB,OAAO;IACP,aAAa;IACb,uBAAuB;EACzB;EACA;IACE,gBAAgB;IAChB,kBAAkB;IAClB,sCAAsC;IACtC,4BAA4B;IAC5B,WAAW;EACb;EACA;IACE,eAAe;IACf,gBAAgB;IAChB,mBAAmB;EACrB;EACA;IACE,6BAA6B;IAC7B,mBAAmB;EACrB;EACA;IACE,aAAa;IACb,mBAAmB;IACnB,eAAe;IACf,mBAAmB;IACnB,SAAS;EACX;EACA;IACE,eAAe;IACf,gBAAgB;EAClB;EACA;IACE,OAAO;EACT;EACA;IACE,gBAAgB;IAChB,eAAe;IACf,iBAAiB;EACnB;EACA;IACE,aAAa;IACb,8BAA8B;IAC9B,eAAe;IACf,gBAAgB;IAChB,gBAAgB;EAClB;EACA;IACE,eAAe;IACf,gBAAgB;EAClB;EACA;IACE,mBAAmB;IACnB,6BAA6B;IAC7B,oBAAoB;EACtB;EACA;IACE,mBAAmB;IACnB,gBAAgB;IAChB,iBAAiB;EACnB;EACA;IACE,iBAAiB;IACjB,gBAAgB;IAChB,mBAAmB;IACnB,WAAW;EACb","sourcesContent":[".order-summary-wrapper {\n    min-width: 280px;\n    max-width: 340px;\n    flex: 1;\n    display: flex;\n    align-items: flex-start;\n  }\n  .order-summary {\n    background: #fff;\n    border-radius: 8px;\n    box-shadow: 0 2px 8px rgba(0,0,0,0.06);\n    padding: 28px 28px 18px 28px;\n    width: 100%;\n  }\n  .order-summary-title {\n    font-size: 22px;\n    font-weight: 700;\n    margin-bottom: 18px;\n  }\n  .order-summary-list {\n    border-bottom: 1px solid #eee;\n    margin-bottom: 18px;\n  }\n  .order-summary-item {\n    display: flex;\n    align-items: center;\n    font-size: 16px;\n    margin-bottom: 10px;\n    gap: 12px;\n  }\n  .order-summary-qty {\n    min-width: 18px;\n    font-weight: 500;\n  }\n  .order-summary-name {\n    flex: 1;\n  }\n  .order-summary-price {\n    font-weight: 500;\n    min-width: 60px;\n    text-align: right;\n  }\n  .order-summary-total-row {\n    display: flex;\n    justify-content: space-between;\n    font-size: 20px;\n    font-weight: 700;\n    margin-top: 10px;\n  }\n  .order-summary-total {\n    font-size: 22px;\n    font-weight: 700;\n  }\n  .restaurant-group {\n    margin-bottom: 20px;\n    border-bottom: 1px solid #eee;\n    padding-bottom: 15px;\n  }\n  .restau