___CSS_LOADER_EXPORT___.push([module.id, `.categories-swiper {
  display: none;
}

.categories-swiper .swiper-scrollbar {
  height: 8px !important;
  background: #f0f0f0;
  border-radius: 4px;
}

.categories-swiper .swiper-scrollbar-drag {
  background: #8e8e8e !important;
  border-radius: 4px;
}

.categories-swiper .swiper-scrollbar-horizontal {
  left: 20px;
  right: 20px;
  width: auto;
}

/* Ensure the scrollbar is always visible */
.swiper-scrollbar-hidden {
  opacity: 1 !important;
}

@media (max-width: 1279px) {
  .categories-swiper {
    display: block;
    padding: 0 20px;
  }

  .categories-swiper .swiper {
    padding: 20px 0;
    touch-action: pan-y pinch-zoom;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
  }

  .categories-swiper .swiper-slide {
    transition: transform 0.3s ease;
  }

  .categories-swiper .swiper-slide-active {
    transform: scale(1);
  }
} `, "",{"version":3,"sources":["webpack://./src/pages/home/carusel/categories/swiper.css"],"names":[],"mappings":"AAAA;EACE,aAAa;AACf;;AAEA;EACE,sBAAsB;EACtB,mBAAmB;EACnB,kBAAkB;AACpB;;AAEA;EACE,8BAA8B;EAC9B,kBAAkB;AACpB;;AAEA;EACE,UAAU;EACV,WAAW;EACX,WAAW;AACb;;AAEA,2CAA2C;AAC3C;EACE,qBAAqB;AACvB;;AAEA;EACE;IACE,cAAc;IACd,eAAe;EACjB;;EAEA;IACE,eAAe;IACf,8BAA8B;IAC9B,iBAAiB;IACjB,yBAAyB;IACzB,sBAAsB;IACtB,qBAAqB;EACvB;;EAEA;IACE,+BAA+B;EACjC;;EAEA;IACE,mBAAmB;EACrB;AACF","sourcesContent":[".categories-swiper {\n  display: none;\n}\n\n.categories-swiper .swiper-scrollbar {\n  height: 8px !important;\n  background: #f0f0f0;\n  border-radius: 4px;\n}\n\n.categories-swiper .swiper-scrollbar-drag {\n  background: #8e8e8e !important;\n  border-radius: 4px;\n}\n\n.categories-swiper .swiper-scrollbar-horizontal {\n  left: 20px;\n  right: 20px;\n  width: auto;\n}\n\n/* Ensure the scrollbar is always visible */\n.swiper-scrollbar-hidden {\n  opacity: 1 !important;\n}\n\n@media (max-width: 1279px) {\n  .categories-swiper {\n    display: block;\n    padding: 0 20px;\n  }\n\n  .categories-swiper .swiper {\n    padding: 20px 0;\n    touch-action: pan-y pinch-zoom;\n    user-select: none;\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n  }\n\n  .categories-swiper .swiper-slide {\n    transition: transform 0.3s ease;\n  }\n\n  .categories-swiper .swiper-slide-active {\n    transform: scale(1);\n  }\n} "],"sourceRoot":""}]);
