  display: flex;
  justify-items: center;
  align-items: center;
  height: 242px;
  background-color: #E5F8BC;
}
.top-banner-title{
  width: 229px;
  height: 64px;
  font-weight: 700;
  font-size: 43.95px;
  line-height: 64px;
  letter-spacing: -0.05em;
  text-align: center;
  color: #2C2C2C;
  margin-left: auto;
  margin-right: auto;
}
.main-title{
  width: 386px;
  height: 43px;
  font-weight: 700;
  font-size: 35.16px;
  line-height: 43px;
  letter-spacing: -0.02em;
  margin-top: 72px;
  margin-left: 56px;
  color: #202020;
}
.main-discovery{
  height: 60px;
  font-family: var(--font-Hind);
  font-weight: 400;
  font-size: 18px;
  line-height: 100%;
  letter-spacing: 0;
  margin-top: 16px;
  margin-left: 56px;
  color: #2C2C2C;
}
.categories-section{
  display: flex;
  justify-content:center;
  flex-wrap: wrap;
  margin: 40px 56px;
  gap: 24px;
}
.categories-section-card{
  width: 427px;
  height: 309px;
}
.categories-section-card-img{
  width: 427px;
  height: 309px;
  border-radius: 8px;
  margin: 0;
}
.categories-section-card-link{
  display: inline-block; /* Устанавливаем элемент как блочный */
  text-decoration: none; /* Убираем подчеркивание */
  border: none; /* Убираем рамку */
  cursor: pointer; /* Добавляем указатель мыши в виде руки для кликабельности */
  text-align: center; /* Выравнивание текста по горизонтали */
  width: 72px;
  height: 45px;
  border-radius: 8px;
  background-color: #202020;
  color: #FFFFFF;
  font-family: var(--font-Hind);
  font-weight: 500;
  font-size: 18px;
  line-height: 45px;
  letter-spacing: 0;
  position: absolute;
  margin-top: 16px;
  margin-left: -411px;
}
@media (max-width: 520px) {
  .main-title{
    width: 100%;
    margin-left: 16px;
    font-size: 31.25px;
    line-height: 40px;
  }
  .main-discovery{
    font-size: 16px;
    margin-left: 16px;
  }
  .categories-section{
    margin-left: 16px;
    margin-right: 0;
  }
  .categories-section-card{
    width: 328px;
    height: 246px;
  }
  .categories-section-card-img{
    width: 328px;
    height: 246px;
  }
  .categories-section-card-link{
    margin-left: -320px;
  }  
} 
@media (max-width: 358px) {
  .main-title{
    height: 80px;
  }
